import React from 'react'
import {
  BrowserRouter as Router,
  Switch,
  Route,
} from "react-router-dom";
import './App.css'
import { Container, Row, Col} from "shards-react";

import Login from './components/login'
import Start from './components/startingScreen'
import Home from './components/home'

function App() {

  return (
    <Router>
        <Switch>
          <Route path="/login">
            <Login />
          </Route>
          <Route path="/home">
            <Home />
          </Route>
          <Route path="/">
            <Start />
          </Route>
        </Switch>
    </Router>
  )
}

export default App